.about-container {
  padding-top: 100px;
  padding-bottom: 60px;
}

.about-title {
  text-align: center;
  margin-bottom: 40px;
}

.about-content {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 10px;
  backdrop-filter: blur(10px);
}

@media (max-width: 600px) {
  .about-container {
    padding-top: 80px;
  }

  .about-content {
    padding: 15px;
  }
}
